* {
    margin: opx;
    padding: 0px;
}

::-webkit-scrollbar {
    width: 7px;
}

::-webkit-scrollbar-track {
    background-color: #0d1117;
}

::-webkit-scrollbar-thumb {
    background: #3e424f;
    border-radius: 20px;
}


.our_target_section {
    display: block;
    padding: 80px 0;
}

.container {
    padding: 0 6.510416vw;
}

.main_title {
    font-family: Epilogue;
    font-size: 30px;
    font-weight: 700;
    line-height: 38.75px;
    text-align: center;
    margin: 0;
    color: #fff;
}

.main_title span {
    background: linear-gradient(90deg, #b721ff, #21d4fd);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    font-weight: 700;
    padding-left: 4px;
}

.our_target_section .target_details {
    margin-top: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 100px;
}

.our_target_section .target_details .target_listing .target_list_box {
    display: flex;
    gap: 16px;
    padding: 20px;
    align-items: center;
    position: relative;
    cursor: pointer;
    max-width: 550px;
}

.our_target_section .target_details .target_listing .target_list_box .target_icons {
    width: 20px;
    height: 20px;
}

.our_target_section .target_details .target_listing .target_list_box .list_details {
    display: block;
}

.our_target_section .target_details .target_listing .target_list_box .list_details h2 {
    font-family: Epilogue;
    font-size: 16px;
    font-weight: 600;
    margin: 0;
    color: #fff;
}

.our_target_section .target_details .target_listing .target_list_box .list_details p {
    font-size: 12px;
    font-weight: 400;
    line-height: 16.2px;
    color: #797979;
    margin: 0;
    height: 0;
    visibility: hidden;
}

.our_target_section .target_details .target_listing .target_list_box .list_details h2 {
    margin: 1px 0 6px;
}

.our_target_section .target_details .target_listing .target_list_box.active {
    align-items: flex-start;
    padding: 16px 16px 26px;
    background: linear-gradient(180deg, hsla(0, 0%, 100%, .1) .39%, hsla(0, 0%, 60%, .1) 100.39%), #0d1117;
    border: none;
}

.our_target_section .target_details .target_listing .target_list_box.active p {
    visibility: visible;
    height: auto;
}

.our_target_section .target_details .target_listing.for_desktop {
    display: block;
}

.our_target_section .target_details .target_listing {
    width: 47%;
    max-height: 500px;
    overflow-y: auto;
}

.our_target_section .target_details .target_img img {
    width: 100% !important;
    max-width: 500px;
    height: auto !important;
}

.our_target_section .target_details .target_img {
    width: 47%;
    text-align: center;
}

.our_target_section .target_details .target_listing .target_list_box:before {
    position: absolute;
    content: "";
    width: 0;
    height: 3px;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(270deg, transparent 50%, rgba(183, 33, 255, .8) 0, rgba(33, 212, 253, .8) 61.75%, hsla(0, 0%, 100%, .1)), linear-gradient(180deg, rgba(13, 17, 23, 0) 69.5%, #0d1117), radial-gradient(63.89% 100% at 50% 100%, rgba(13, 17, 23, 0) 18.14%, #0d1117 100% 50%);
    background-size: 200% 100%;
    background-position: 100%;
    transition: background-position 25s ease-in-out;
}

.our_target_section .target_details .target_listing .target_list_box.active:before {
    width: -webkit-fill-available;
    background-position: 0%;
}

@media only screen and (max-width: 992px) {
    .our_target_section .target_details {
        gap: 10px;
    }

    .our_target_section .main_title {
        max-width: 462px;
        margin: 0 auto;
    }
}

@media only screen and (max-width: 1207px) {
    .our_target_section .target_details {
        gap: 30px;
    }
}